-items:center;justify-content:center}.principal_container .task_section .tasks_list .task .task_actions button:first-of-type{margin-right:10px}.principal_container .task_section .tasks_list .task .task_actions button svg{color:#d1e5e7;font-size:1.8em}.principal_container .task_section .tasks_list .task .edit_input{background:transparent;border:none;border:1px solid #d1e5e7;border-radius:10px;color:#d1e5e7;font-size:1.3em;outline:none;padding:2px 8px;width:75%;display:none}.principal_container .task_section .tasks_list .task .edit_input::-webkit-input-placeholder{color:#d1e5e7;font-size:.9em}.principal_container .task_section .tasks_list .task .edit_input:-ms-input-placeholder{color:#d1e5e7;font-size:.9em}.principal_container .task_section .tasks_list .task .edit_input::placeholder{color:#d1e5e7;font-size:.9em}

